.ng2-c-data-state {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding-bottom: 80px;
  padding-top: 80px;
  width: 100%; }

.ng2-c-data-state--no-spacing {
  padding-bottom: 0;
  padding-top: 0; }

.ng2-c-data-state__icon {
  color: #cccccc;
  display: inline-block;
  font-size: 50px;
  height: 50px;
  text-align: center;
  width: 100%; }

.ng2-c-data-state__message {
  color: #9b9b9b;
  display: block;
  max-width: 200px;
  text-align: center; }

.ng2-c-data-state__icon + .ng2-c-data-state__message {
  margin-top: 20px; }

.ng2-c-data-state__button {
  margin-top: 50px; }
